                        <div class="bi-charts-inner">
                            <div id="chart-operation-3" class="bi-chart bi-chart-1"></div>
                        </div>
                    </div>
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            {{all[4].title}}
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-bl-tabs">
                            <div class="bi-bt-inner">
                                <div v-for="(item, index) in list['4']['name']"
                                     :class="{'bi-bt-item-active': index === list['4']['index'] }"
                                     class="bi-bt-item">{{ item }}</div>
