                            <div id="chart-operation-4" class="bi-chart bi-chart-2"></div>
                        </div>
                    </div>
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            {{all[5].title}}
                            <div class="bi-bl-full"></div>
                        </div>
