                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-operation-5" class="bi-chart bi-chart-2"></div>
                        </div>
                    </div>
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            {{all[6].title}}
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-bl-tabs">
                            <div class="bi-bt-inner">
                                <div v-for="(item, index) in list['6']['name']"
