<script>
    require('echarts/map/js/china.js')
    import { OperationData } from './../data'
    import { IconsMap, ColorsMap } from './../common/constants'
    // import Utils from './../common/utils'

    export default {
        components: {},
        data() {
            return {
                index: 0,
                html: '',
                data: OperationData,
                charts: OperationData.result.data.groups[0].charts,
                ol: [],
                iconsMap: IconsMap,
                colorsMap: '',
                dividerColor: ColorsMap.dividerColor,
                fontColor: ColorsMap.fontColor,
                lineStyle: {
                    lineStyle: {
                        color: ColorsMap.dividerColor,
                        width: 1,
                        type: 'solid'
                    },
                },
                textStyle: {
                    textStyle: {
                        color: ColorsMap.fontColor,
                    },
                },
                list: {
                    1: {
                        index: 0,
                        name: [],
                    },
                    2: {
                        index: 0,
                        name: [],
                    },
                    3: {
                        index: 0,
                        name: [],
                    },
                    4: {
                        index: 0,
                        name: [],
                    },
                    5: {
                        index: 0,
                        name: [],
                    },
                    6: {
                        index: 0,
                        name: [],
                    },
                },
                ratio: [],
                all: {
                    1:{},
                    2:{},
                    3:{},
                    4:{},
                    5:{},
                    6:{},
                },
                els: [
                    'chart-operation-1',
                    'chart-operation-2',
                    'chart-operation-3',
                    'chart-operation-4',
                    'chart-operation-5',
                    'chart-operation-6',
                ],
                chartOpts: {},
                chartObjs: {},

            }
        },
        mounted() {
            // this.getChartsData()
            // console.log(this.charts)
            // this.getData()
            // this.charts = res.result.data.groups[0].charts
            this.ol = this.charts.map(item=>item.data)
            // console.log(this.ol)
            // this.drawCharts()

            this.getGroupData()
            this.getChartsObj()
            this.setOptions()
            this.initCharts()
            this.dataHandle()
            this.setChartOpts()



        },
        methods: {
            drawCharts() {

            },
            getChartsObj(){
                // echarts 实例化
                this.els.map((item, i)=>{
                    this.chartObjs[i+1+''] = this.$echarts.init(document.getElementById(item))
                })
            },
            initCharts() {
                for (let i = 1; i < this.els.length + 1; i++) {
                    let key = i + ''
                    this.chartObjs[key].setOption(this.chartOpts[key])
                }
            },
            getChartsData() {
                this.$http.ajax({
                    url: '/api/v2/technology/all/10',
                    data: {}
                }).then(res => {
                    console.log(res)
                    // this.charts = res.result.data.groups[0].charts
                    // this.ol = this.charts.map(item=>item.data)
                    // console.log(this.ol)
                    // this.drawCharts()
                }).catch(e => {
                    console.log(e)
                })
            },
            dataHandle() {
                ((id)=>{
                    let data = this.all[id].data
                    let dy = []
                    let legend = []
                    dy.push({
                        // name: data.yAxis,
                        type: 'line',
                        stack: '线索量',
                        // areaStyle: {},
                        data: data.yAxis,
                        symbol: 'none',
                    })

                    this.list[id] = {
                        index: 0,
                        name: [
                            '总概览',
                        ],
                        legend: legend,
                        dx: data.xAxis,
                        dy,
                    }
                })('1');

                ((id)=>{
                    let data = this.all[id].data
                    let bars = []
                    let legend = []
                    data.bars.map((item,i)=>{
                        legend.push(item.name)
                        let obj = {
                            name: item.name,
                            type: 'bar',
                            stack: '线索',
                            data: item.value.map(val=>val/10000),
                            barWidth: 10,
                        }
                        if(i===bars.length-1){
                            obj = Object.assign({},obj, {
                                itemStyle: {
                                    barBorderRadius: [7,7,0,0]
                                },
                            })
                        }
                        bars.push(obj)
                    })
                    let lines = {
                        name: data.lines[0].name,
                        type: 'line',
                        data: data.lines[0].value.map(val=>val/10000),
                        yAxisIndex: 1,
                        symbol: 'none',
                        ...this.linesStyle,
                    }
                    legend.push(data.lines[0].name)
                    this.list[id] = {
                        index: 0,
                        name: [
                            '总概览',
                            ...data.bars.map(item=>item.name),
                        ],
                        legend: legend,
                        dx: data.xAxis,
                        dy: [
                            ...bars,
                            lines
                        ],
                    }
                })('2');

                ((id)=>{
                    let data = this.all[id].data
                    let dy = []
                    let legend = []
                    console.log(data)
                    dy.push({
                        // name: data.yAxis,
                        type: 'line',
                        stack: '线索量',
                        // areaStyle: {},
                        data: data.yAxis,
                        symbol: 'none',
                    })

