                    let data = this.all[id].data
                    let dy = []
                    let legend = []
                    data.yAxis.map((item,i)=>{
                        legend.push(item.name)
                        dy.push({
                            name: item.name,
                            type: 'bar',
                            stack: '55',
                            data: item.value.map(val=>val/10000),
                            barWidth: 10,
                            barGap: "-100%",
                            // zlevel: (20-i),
                            // itemStyle: {
                            //     barBorderRadius: [7,7,0,0]
                            // },
                        })
                    })
                    this.list[id] = {
                        index: 0,
                        name: [
                            '总概览',
                            ...legend,
                        ],
                        legend: legend,
                        dx: data.xAxis,
                        dy: dy,
                    }
                })('4');

                ((id)=>{
                    let data = this.all[id].data
                    let dy = []
                    let legend = []
                    data.yAxis.map((item,i)=>{
                        legend.push(item.name)
                        dy.push({
                            name: item.name,
                            type: 'bar',
                            stack: '55',
                            data: item.value.map(val=>val/10000),
                            barWidth: 10,
                            barGap: "-100%",
                            // zlevel: (20-i),
                            // itemStyle: {
                            //     barBorderRadius: [7,7,0,0]
                            // },
                        })
                    })
                    this.list[id] = {
                        index: 0,
                        name: [
                            '总概览',
                            ...legend,
                        ],
                        legend: legend,
                        dx: data.xAxis,
                        dy: dy,
                    }
                })('5');

