                    let data = this.all[id].data
                    let dy = []
                    let legend = []
                    data.yaxis.map(item=>{
                        legend.push(item.name)
                        dy.push({
                            name: item.name,
                            type: 'line',
                            stack: '线索量',
                            areaStyle: {},
                            data: item.value.map(val=>val/10000),
                            symbol: 'none',
                        })
                    })

                    this.list[id] = {
                        index: 0,
                        name: [
                            '总概览',
                            ...legend,
                        ],
                        legend: legend,
                        dx: data.xaxis,
                        dy,
                    }
                })('6');

            },
            setChartOpts() {
                ((id)=>{
                    let item = this.list[id]
                    this.chartObjs[id].setOption({
                        legend: {
                            data: item.legend,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: item.dy,
                    })
                })('1');

                ((id)=>{
                    let item = this.list[id]
                    this.chartObjs[id].setOption({
                        legend: {
                            data: item.legend,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: item.dy,
                    })
                })('2');

                ((id)=>{
                    let item = this.list[id]
                    this.chartObjs[id].setOption({
                        legend: {
                            data: item.legend,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: item.dy,
