                    let item = this.list[id]
                    this.chartObjs[id].setOption({
                        legend: {
                            data: item.legend,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: item.dy,
                    })
                })('4');

                ((id)=>{
                    let item = this.list[id]
                    this.chartObjs[id].setOption({
                        grid: {
                            bottom: 50,
                        },
                        legend: {
                            data: item.legend,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: item.dy,
                    })
                })('5');

                ((id)=>{
                    let item = this.list[id]
                    this.chartObjs[id].setOption({
                        legend: {
                            data: item.legend,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
