                let res = this.charts
                this.ratio = JSON.parse(res[0].data[0].data)
                console.log(this.ratio)
                this.all[1] = {
                    title: res[0].data[1].title,
                    data: JSON.parse(res[0].data[1].data),
                    pop: res[0].data[1].linkto.uri,
