                        right: 10,
                        top: 40,
                        bottom: 50,
                        containLabel: true
                    },
                    legend: {
                        data: [],
                        bottom: 10,
                        itemGap: 15,
                        height: 30,
                        width: '80%',
                        itemWidth: 20,
                        itemHeight: 10,
                        textStyle: {
                            color: this.fontColor,
                        },
                    },
                    color: ['#C0C28D', '#C07C71'],
                    ...this.textStyle,
                    xAxis : [
                        {
                            type : 'category',
                            boundaryGap : false,
                            data : [],
                            axisTick: {
                                show: false,
                            },
                            axisLine: {
                                ...this.lineStyle,
                            },
                            splitLine: {
                                show: true,
                                interval: 1,
                                ...this.lineStyle,
                            },
                        },
                    ],
                    yAxis : [
                        {
                            type : 'value',
                            name: '%',
                            axisLine: {
                                ...this.lineStyle,
                            },
                            axisTick: {
                                ...this.lineStyle,
                            },
                            splitLine: {
                                ...this.lineStyle,
                            },
                        },
                    ],
                    series : [],
                }

                this.chartOpts['2'] = {
                    color: [
                        '#8723B7',
                        '#10A6AC','#D2575F','#F2D346','#F09547','#D655C7','#62B7F1'
                    ],
                    grid: {
                        left: 10,
                        top: 40,
                        right: 10,
                        bottom: 80,
                        containLabel: true,
                    },
                    legend: {
                        data: [],
                        bottom: 10,
                        itemGap: 15,
                        height: 30,
                        width: '80%',
                        itemWidth: 20,
                        itemHeight: 10,
                        ...this.textStyle,
                        tooltip: {
                            show: true
                        }
                    },
                    xAxis: [
                        {
                            type: 'category',
                            data: [],
                            axisPointer: {
                                type: 'shadow'
                            },
                            axisTick: {
                                show: false,
                            },
                            axisLine: {
                                ...this.lineStyle,
                            },
                        }
                    ],
                    yAxis: [
                        {
                            type: 'value',
                            name: '万',
                            // min: 0,
                            // max: 250,
                            // interval: 234,
                            axisLabel: {
                                formatter: '{value}'
                            },
                            splitLine: {
                                show: true,
                                interval: 1,
                                ...this.lineStyle,
                            },
                            axisTick: {
                                ...this.lineStyle,
                            },
                            axisLine: {
                                ...this.lineStyle,
                            },
                        },
                        {
                            type: 'value',
                            name: '万',
                            // min: 0,
                            // max: 25,
                            // interval: 7118,
                            axisLabel: {
                                formatter: '{value}'
                            },
                            splitLine: {
                                show: false,
                            },
                            axisLine: {
                                ...this.lineStyle,
                            },
                            axisTick: {
                                ...this.lineStyle,
                            },
                        }
                    ],
                    ...this.textStyle,
                    series: [],
                }

                this.chartOpts['3'] = Object.assign({}, this.chartOpts['1'])

                this.chartOpts['4'] = {
                    color: [
                        '#8723B7',
                        '#10A6AC','#D2575F','#F2D346','#F09547','#D655C7','#62B7F1'
                    ],
                    grid: {
                        left: 10,
                        top: 40,
                        right: 10,
                        bottom: 80,
                        containLabel: true,
                    },
                    legend: {
                        data: [],
                        bottom: 10,
                        itemGap: 15,
                        height: 30,
                        width: '70%',
                        itemWidth: 20,
                        itemHeight: 10,
                        ...this.textStyle,
                        tooltip: {
                            show: true
                        }
                    },
                    xAxis: [
                        {
                            type: 'category',
                            data: [],
                            axisPointer: {
                                type: 'shadow'
                            },
                            axisTick: {
                                show: false,
                            },
                            axisLine: {
                                ...this.lineStyle,
                            },
                        }
                    ],
                    yAxis: [
                        {
                            type: 'value',
                            name: '万条',
                            // min: 0,
                            // max: 250,
                            // interval: 234,
                            axisLabel: {
                                formatter: '{value}'
                            },
                            splitLine: {
                                show: true,
                                interval: 1,
                                ...this.lineStyle,
                            },
                            axisTick: {
                                ...this.lineStyle,
                            },
                            axisLine: {
                                ...this.lineStyle,
                            },
                        },
                    ],
                    ...this.textStyle,
                    series: [],
                }

                this.chartOpts['5'] = Object.assign({}, this.chartOpts['4'])

                this.chartOpts['6'] = {
                    grid: {
                        left: 10,
                        right: 10,
                        top: 40,
                        bottom: 50,
                        containLabel: true
                    },
                    legend: {
                        data: [],
                        bottom: 10,
                        itemGap: 15,
                        height: 30,
                        width: '80%',
                        itemWidth: 20,
                        itemHeight: 10,
                        textStyle: {
                            color: this.fontColor,
                        },
                    },
                    // color: ['#C0C28D', '#C07C71'],
                    ...this.textStyle,
                    xAxis : [
                        {
                            type : 'category',
                            boundaryGap : false,
                            data : [],
                            axisTick: {
                                show: false,
                            },
                            axisLine: {
                                ...this.lineStyle,
                            },
                            splitLine: {
                                show: true,
                                interval: 1,
                                ...this.lineStyle,
                            },
                        },
                    ],
                    yAxis : [
                        {
                            type : 'value',
                            name: '万',
                            axisLine: {
                                ...this.lineStyle,
                            },
                            axisTick: {
                                ...this.lineStyle,
                            },
                            splitLine: {
                                ...this.lineStyle,
                            },
                        },
                    ],
                    series : [],
                }


            },
