    }
</script>

<style scoped lang="scss">
    .bi-chart-1{
        height: 600px;
    }
    .bi-chart-2{
        height: 900px;
    }
    .bi-chart-3,
    .bi-chart-4,
    .bi-chart-6{
        height: 800px;
